@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300;900&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{-ms-scroll-chaining:none;overscroll-behavior:none}

.circles-field{position:fixed;top:0;left:0;bottom:0;right:0}.circle{position:absolute;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;color:#171a1c;font-size:7em;font-weight:900;width:100px;height:100px;transform:translate(calc(100px/-2), calc(100px/-2));background-image:radial-gradient(rgba(119,255,51,0.7), rgba(51,255,153,0.7));border-radius:50%;display:flex;justify-content:center;align-items:center;-webkit-animation:cir_pulse 1s linear infinite;animation:cir_pulse 1s linear infinite}.circle__border{width:calc(150px);height:calc(150px);border:10px solid rgba(119,255,51,0.7);border-left:10px solid transparent;border-radius:50%;background-clip:padding-box;padding:calc(66.66667px);-webkit-animation:cir_rotate 1.5s linear infinite;animation:cir_rotate 1.5s linear infinite}.circle__border.winner{-webkit-animation:cir-border_winner 1.5s linear forwards;animation:cir-border_winner 1.5s linear forwards}.circle__number{position:absolute}@-webkit-keyframes cir_pulse{50%{transform:translate(calc(100px/-2), calc(100px/-2)) scale(0.9)}100%{transform:translate(calc(100px/-2), calc(100px/-2))}}@keyframes cir_pulse{50%{transform:translate(calc(100px/-2), calc(100px/-2)) scale(0.9)}100%{transform:translate(calc(100px/-2), calc(100px/-2))}}@-webkit-keyframes cir_rotate{100%{transform:rotate(360deg)}}@keyframes cir_rotate{100%{transform:rotate(360deg)}}@-webkit-keyframes cir_winner{80%{width:300vh;height:300vh;transform:translate(calc(300vh/-2), calc(300vh/-2));left:50%;top:50%;font-size:24em}100%{width:300vh;height:300vh;transform:translate(calc(300vh/-2), calc(300vh/-2));left:50%;top:50%;font-size:24em}}@keyframes cir_winner{80%{width:300vh;height:300vh;transform:translate(calc(300vh/-2), calc(300vh/-2));left:50%;top:50%;font-size:24em}100%{width:300vh;height:300vh;transform:translate(calc(300vh/-2), calc(300vh/-2));left:50%;top:50%;font-size:24em}}@-webkit-keyframes cir-border_winner{100%{border-width:250vh}}@keyframes cir-border_winner{100%{border-width:250vh}}

.app{position:fixed;top:0;left:0;bottom:0;right:0;background-color:#171a1c;overflow:hidden;font-family:'Chivo', sans-serif}.app__desktop{width:100%;height:100vh;padding:10vw;display:flex;justify-content:center;align-items:center;color:#f5f5f5;font-size:1.4em}.app__count{position:absolute;top:0;left:0;right:0;height:50px;display:flex;justify-content:center;align-items:center;color:white;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

